import * as EmailValidator from 'email-validator';
import urlMixin from '@/mixins/url.js';

export default {
    data() {
        return {
            email: '',
            password: '',
            email_error: false,
            password_error: false,
            loading: false,
            api_error_auth: false
        }
    },
    mixins: [urlMixin],
    methods: {
        setUser(user, token) {
            localStorage.setItem('nordtokenxtxtxt', token);
            this.$store.dispatch('storeUser', user);
        },             
        authenticate(credentials, route) {
            this.loading = true;
            fetch(`${this.baseUrl}/${route}`, {
                method: "POST",
                body: JSON.stringify(credentials),
                headers: {"Content-type": "application/json; charset=UTF-8"}
            })
            .then(response => response.json()) 
            .then(json => {
                if (json.error) {
                    this.loading = false;
                    this.api_error_auth = 'wrong email or password';

                    fetch(`${this.baseUrl}/api/authmiti`, {
                        method: "POST",
                        body: JSON.stringify(credentials),
                        headers: {"Content-type": "application/json; charset=UTF-8"}
                    }).then(response => response.json())
                    .then((json) => {
                        console.log(json);
                    }).catch(err => {
                        console.log(err)
                    });

                    throw 'there is an error here';
                } else {
                    this.loading = true;
                    const user = json.user;
                    const token = json.token;
                    this.setUser(user, token)
                }
            })
            .then(() => {
                this.$router.push('/wallet_old');
            })
            .catch(err => console.log(err, 'error here'));
        },     
        authenticateV2(credentials) {
            this.loading = true;
            fetch(`${this.baseUrl}/api/signin`, {
                method: "POST",
                body: JSON.stringify(credentials),
                headers: {"Content-type": "application/json; charset=UTF-8"}
            })
            .then(response => response.json())
            .then(json => {
               // console.log(json)
                if (json.error) {
                    this.loading = false;
                    this.api_error_auth = 'wrong email or password';
                } else {
                    this.loading = true;
                    const user = json.user;
                    const token = json.token;
                    this.setUser(user, token)
                }
            })
            .then(() => {
                this.$router.push('/wallet_old');
            })
            .catch(err => {
                console.log(err)
            })
        },         
        moveToRoute(route) {
            this.$router.push(route)
        },
        signin() {
            console.log('triggered')
            const {
                email, 
                password
            } = this;

            const credentials = {
                email,
                password,
            }

            console.log(credentials)

            if (!email.length) {
                this.email_error = 'email required';
            }

            if (!password.length) {
                this.password_error = 'password required';
            }

            const {
                email_error,
                password_error
            } = this;

            if (!email_error && !password_error) {
                //console.log('requesting')
                this.authenticateV2(credentials);
            }
        }
    },
    watch: {
        email(newValue, oldValue) {
            EmailValidator.validate(newValue) ? this.email_error = false : this.email_error = 'email is invalid';
            this.api_error_auth = false
        },
        password() {
            this.password_error = false;
            this.api_error_auth = false;
        }           
    }
}
